.our-story{
    .container{
        display: flex;
        align-items: center;
        position: relative;
        [class *= "col"]{
            width: 50%;
        }
        .progress{
            display: flex;
            justify-content: center;
            align-items: center;
            ul{
                position: relative;
                &:first-child::after{
                    content: "";
                    position: absolute;
                    display: block;
                    width: 4px;
                    height: 100%;
                    top: -20%;
                    right: -15px;
                    background-color: #d4d4d4;
                    z-index: -2;
                    transition: 1.5s;
                }
                &.ready:first-child::before{
                    content: "";
                    display: block;
                    width: 4px;
                    height: 0;
                    position: absolute;
                    top: -20%;
                    right: -15px;
                    background-color: #c71222;
                    z-index: -1;
                    transition: 1.5s;
                }
                &.started{
                    + ul{
                        li:first-child{
                            .event::before{
                                border: 4px solid #c71222;
                                transition: 2s;
                            }
                        }
                    }
                    &:first-child::before{
                        content: "";
                        display: block;
                        width: 4px;
                        height: 40%;
                        position: absolute;
                        top: -20%;
                        right: -15px;
                        background-color: #c71222;
                        z-index: -1;
                        transition: 1.5s;
                    }
                }
                &.event-one{
                    + ul{
                        li:nth-child(1){
                            .event::before{
                                border: 4px solid #c71222;
                            }
                        }
                        li:nth-child(2){
                            .event::before{
                                border: 4px solid #c71222;
                                transition: 2s;
                            }
                        }
                    }
                    &:first-child::before{
                        content: "";
                        display: block;
                        width: 4px;
                        height: 60%;
                        position: absolute;
                        top: -20%;
                        right: -15px;
                        background-color: #c71222;
                        z-index: -1;
                        transition: 1.5s;
                    }
                }
                &.event-two{
                    + ul{
                        li:nth-child(1){
                            .event::before{
                                border: 4px solid #c71222;
                            }
                        }
                        li:nth-child(2){
                            .event::before{
                                border: 4px solid #c71222;
                            }
                        }
                        li:nth-child(3){
                            .event::before{
                                border: 4px solid #c71222;
                                transition: 2s;
                            }
                        }
                    }
                    &:first-child::before{
                        content: "";
                        display: block;
                        width: 4px;
                        height: 80%;
                        position: absolute;
                        top: -20%;
                        right: -15px;
                        background-color: #c71222;
                        z-index: -1;
                        transition: 1.5s;
                    }
                }
                &.ended{
                    + ul{
                        li{
                            .event::before{
                                border: 4px solid #c71222;
                                transition: 2s;
                            }
                        }
                    }
                    &:first-child::before{
                        content: "";
                        display: block;
                        width: 4px;
                        height: 100%;
                        position: absolute;
                        top: -20%;
                        right: -15px;
                        background-color: #c71222;
                        z-index: -1;
                        transition: 1.5s;
                    }
                }
                li{
                    line-height: 20px;
                    margin: 100px auto;
                    *{
                        vertical-align: middle;
                        font-weight: 400;
                        margin-right: 10px;
                    }
                    span{
                        font-size: 16px;
                        line-height: 26px;
                        font-family: "Proxima Nova";
                        color: #d4d4d4;
                    }
                    .event{
                        color: #141720;
                        &::before{
                            content: "";
                            display: inline-block;
                            vertical-align: middle;
                            margin-right: 10px;
                            width: 18px;
                            height: 18px;
                            position: relative;
                            background-color: #ffffff;
                            border: 4px solid #d4d4d4;
                            border-radius: 50%;
                        }
                    }
                }
            }
        }
        .col-right{
            &.ready{
                .story-content{
                    .ready{
                        display: block;
                        animation: zoomIn;
                        animation-duration: 1.5s;
                    }
                    .started{
                        display: none;
                    }
                    .event-one{
                        display: none;
                    }
                    .event-two{
                        display: none;
                    }
                    .ended{
                        display: none;
                    }
                }
            }
            &.started{
                .story-content{
                    .started{
                        display: block;
                        animation: zoomIn;
                        animation-duration: 1.5s;
                    }
                    .ready{
                        display: none;
                    }
                    .event-one{
                        display: none;
                    }
                    .event-two{
                        display: none;
                    }
                    .ended{
                        display: none;
                    }
                }
            }
            &.event-one{
                .story-content{
                    .event-one{
                        display: block;
                        animation: zoomIn;
                        animation-duration: 1.5s;
                    }
                    .ready{
                        display: none;
                    }
                    .started{
                        display: none;
                    }
                    .event-two{
                        display: none;
                    }
                    .ended{
                        display: none;
                    }
                }
            }
            &.event-two{
                .story-content{
                    .event-two{
                        display: block;
                        animation: zoomIn;
                        animation-duration: 1.5s;
                    }
                    .ready{
                        display: none;
                    }
                    .started{
                        display: none;
                    }
                    .event-one{
                        display: none;
                    }       
                    .ended{
                        display: none;
                    }       
                }
            }
            &.ended{
                .story-content{
                    .ended{
                        display: block;
                        animation: zoomIn;
                        animation-duration: 1.5s;
                    }
                    .ready{
                        display: none;
                    }
                    .started{
                        display: none;
                    }
                    .event-one{
                        display: none;
                    }       
                    .event-two{
                        display: none;
                    }       
                }
            }
            .story-content{
                padding: 144px 0 245px 125px;
                h2{
                    font-size: 28px;
                    letter-spacing: 1px;
                    font-weight: 400;
                    margin-bottom: 55px;
                    font-family: "Proxima Nova";
                    color: #2b2b2b;
                }
                p{
                    font-size: 15px;
                    line-height: 26px;
                    letter-spacing: 0.5px;
                    font-family: "Proxima Nova";
                    white-space:pre-line;
                    color: #5d5f65;
                }
            }
        }
    }
}

@media screen and (max-width:$lg) {
    .our-story{
        .container{
            flex-direction: column;
            .progress{
                ul{
                    &:first-child::after{
                        top: -3%;
                    }
                    &.event-one{
                        &:first-child::before{
                            height: 70%;
                        }
                    }
                    &.event-two{
                        &:first-child::before{
                            height: 95%;
                        }
                    }
                    &.ended{
                        &:first-child::before{
                            height: 120%;
                        }
                    }
                    li{
                        &:last-child{
                            margin-bottom: 0;
                        }
                    }
                }
            }
            .col-right{
                .story-content{
                    padding: 60px 0 125px;
                }
            }
        }
    }
}

@media screen and (max-width:$md) {
    .our-story{
        .container{
            [class *= "col"]{
                width: 100%;
            }
        }
    }
}